import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  enumColumn,
  getSelectedItem,
  InputGroupInline,
  isEmpty,
  OrderType,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ContribuicaoSindicalPatronal } from '../../../../models/dtos/mfp/contribuicaoSindicalPatronal';
import { TIPO_CONTRIBUICAO_SINDICAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoContribuicaoSindicalEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import SindicatoIS from '../../../components/mfp/sindicatoIS/SindicatoIS';
import ContribuicaoSindicalPatronalInfoEditView from './ContribuicaoSindicalPatronalInfoEditView';

interface ContribuicaoSindicalPatronalEditViewProps extends EditViewProps<ContribuicaoSindicalPatronal> {}

const ContribuicaoSindicalPatronalEditView: FC<ContribuicaoSindicalPatronalEditViewProps & RouteComponentProps> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<ContribuicaoSindicalPatronal>({
    defaultValues: { empresaMFP: { id: globalParameter.empresaMFP.id }, ...selectedItem },
    mode: 'onBlur',
  });

  const defaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'ano',
      order: OrderType.ASC,
    },
  ];

  const contribuicaoSindicalInfoProps: DetailManagerFormProps = {
    columns: [
      enumColumn(
        {
          dataField: 'tipoContribuicaoSindical',
          sort: true,
          text: 'Tipo de Contribuiçao Sindical',
        },
        TIPO_CONTRIBUICAO_SINDICAL_ENUM_VALUES
      ),
      {
        dataField: 'ano',
        text: 'Ano',
      },
      {
        dataField: 'valorContribuicao',
        text: 'Valor',
      },
    ],
    detailView: ContribuicaoSindicalPatronalInfoEditView,
    detailViewSize: 'large',
    formProps,
    propName: 'contribuicaoSindicalPatronalInfos',
    title: 'Contribuição Sindical Patronal',
  };

  const validateExtras = () => {
    const erros = [];
    if (isEmpty(formProps.getValues().contribuicaoSindicalPatronalInfos)) {
      erros.push('É obrigatório informar pelo menos uma Contribuição Sindical.');
    }
    return erros;
  };

  return (
    <WindowComponent title="Manutenção da Contribuição Sindical Patronal">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline hidden={true} formProps={formProps} id="empresaMFP.id" />
          </Col>
        </Row>
        <Row>
          <Col>
            <SindicatoIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="Sindicato"
              placeholder="Selecione um Sindicato"
              id="sindicato"
              isPrependLabel={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <DetailManagerForm
              {...props}
              defaultSortDirection={OrderType.DESC}
              defaultSorted={defaultSortProps}
              {...contribuicaoSindicalInfoProps}
              keyField="rowIndex"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ContribuicaoSindicalPatronalEditView;
