import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { ContribuicaoSindicalPatronalInfo } from '../../../../models/dtos/mfp/contribuicaoSindicalPatronalInfo';
import { TIPO_CONTRIBUICAO_SINDICAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoContribuicaoSindicalEnum';

const ContribuicaoSindicalPatronalInfoEditView: FC<DetailEditViewFormProps<ContribuicaoSindicalPatronalInfo>> = props => {
  const formProps = useForm<ContribuicaoSindicalPatronalInfo>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            id="tipoContribuicaoSindical"
            label="Tipo Contribuiçao Sindical"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            placeholder="Informe o Ambiente de Trabalho"
            inputDropDownProps={{ optionsProvider: TIPO_CONTRIBUICAO_SINDICAL_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, min: 1 } }}
            isPrependLabel={true}
            id="valorContribuicao"
            label="Valor da Contribuição Sindical"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 4, ...onlyNumber(0, 4), min: 1930, max: 3000 } }}
            isPrependLabel={true}
            id="ano"
            label="Ano"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ContribuicaoSindicalPatronalInfoEditView;
